@import url('https://fonts.googleapis.com/earlyaccess/notosanstc.css');

@function imgPathBuild($arr) {
  $imgBasePath: '../images/';
  $result: '';
  @for $i from 1 through length($arr) {
    $result: $result + 'url("' + $imgBasePath + nth($arr, $i) + '") '; }

  @return $result; }

// variables
$wrapWidth: 450px;
$wrapHeight: 812px;
$nBaseColor: #fff;
$dBaseColor: #221714;
$moonColor: #f3e727;
$sunColor: #d11577;
$margin: 15px;
$footArea: 13.5vh;
$headArea: (100vh - $footArea);
$towerHeight: 55vh;
$towerWidth: ($towerHeight * 309 / 480);
$towerMarginRight: -($towerWidth / 5);
$eyePosX: '100% - #{$towerWidth / 2 + $towerMarginRight}';
$eyePosY: '100% - 30vh';
$fadeTime: 3;

body {
  $preloadImg: ('');

  font-size: 4vw;
  background: {
    image: linear-gradient(to bottom, #4b2f93, #4253c1 55%, #2048aa 60%, #5f98c9); }
  min-height: 100vh;
  font-family: 'Century Gothic', 'Noto Sans TC';

  &.day {
    background: {
      image: linear-gradient(to bottom, #d37e0a, #f0c41c 30%, #f3e733); } }

  &::after {
    position: absolute;
    width: 0;
    height: 0;
    overflow: hidden;
    z-index: -1;
 } }    // content: #{imgPathBuild($preloadImg)}

.wrap {
  display: flex;
  margin: auto;
  flex-direction: column;
  min-height: 100vh;
  position: relative;
  overflow: hidden;
  max-width: $wrapWidth;
  color: $nBaseColor;

  &::before {
    content: '';
    position: absolute;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    background: {
      repeat: no-repeat;
      position: center top;
      size: cover; }
    z-index: 0; }

  &--night {
    &::before {
      background: {
        image: url('../images/cal/bg-night.jpg'); } }

    &::after {
      content: '';
      position: absolute;
      background: {
        image: url('../images/cal/nightMoon.png');
        repeat: no-repeat;
        size: cover; }
      width: 15vh;
      height: 15vh * (210 / 212);
      left: 50%;
      top: 15vh;
      z-index: 1;
      transform: translateX(-50%) rotate(0) scale(1);
      opacity: 1;
      will-change: transform, left, top, opacity; }

    .day & {
      &::after {
        transform: translateX(-50%) scale(0.2);
        left: 5vh;
        top: 0;
        opacity: 0.2;
        transition: top 1s, left 1s, transform 1s, opacity 1s;
        transition-timing-function: ease-out; } } }

  &--day {
    position: absolute;
    width: 100%;
    height: 100%;
    z-index: 10;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    clip-path: circle(0% at calc(#{$eyePosX}) calc(#{$eyePosY}));
    opacity: 0;

    .day & {
      transition: clip-path 3s, opacity 1.5s;
      clip-path: circle(200% at calc(#{$eyePosX}) calc(#{$eyePosY}));
      opacity: 1; }

    &::before {
      background: {
        image: url('../images/cal/bg-day.jpg'); } } } }

.cal-box {
  position: relative;
  z-index: 2;
  margin-left: $margin;
  margin-right: $margin;
  height: $headArea;
  padding-top: $margin; }

.cal-spHead {
  display: flex; }

.cal-lunar,
.cal-spDay {
  border: 1px solid $nBaseColor; }

.cal-lunar {
  width: 60%;
  padding: .1em .3em;
  display: flex;
  align-items: center;
  font-size: 1.8em;
  white-space: nowrap;

  &__val {
    color: $moonColor;
    margin-left: 0.5em;
    text-align: right;
    width: 100%; } }

.cal-spDay {
  margin-left: $margin;
  width: 40%;
  color: $moonColor;
  text-align: center;
  font-size: 1.5em;
  padding: .1em;
  display: flex;
  flex-direction: column;
  justify-content: center; }

.cal-date__val {
  display: block;
  text-align: center;
  color: $moonColor;
  letter-spacing: .2em;
  white-space: nowrap; }

.cal-manjushri {
  display: flex;
  border: 1px solid $nBaseColor;
  margin-top: .5em;
  font-size: 1.3em;
  padding: .1em .3em;
  letter-spacing: .3em;
  align-items: center;
  white-space: nowrap;

  &__val {
    font-size: 1.6em;
    margin-left: 1em;
    color: #aaa;

    &.good {
      color: $moonColor; } } }

.preface {
  margin-top: 13.5vh;
  font-size: 1.1em;
  line-height: 1.3;
  letter-spacing: .2em; }

.btn {
  border-radius: 0;
  border-width: 0;
  color: #fff;
  letter-spacing: .2em;
  font-size: 1.2em;
  padding-left: 1em;
  padding-right: 1em;
  position: relative;

  &--bless {
    background-image: linear-gradient(to bottom, #0e68aa, #1a2d78);

    &::after {
      content: '';
      position: absolute;
      width: 43px;
      height: 39px;
      background-image: url('../images/cal/icon-hand.png');
      bottom: -25px; } } }

.page-foot {
  position: absolute;
  z-index: 1;
  bottom: 0;
  left: 0;
  width: 100%;
  color: $moonColor;

  &::before {
    content: '';
    position: absolute;
    width: 140%;
    height: 100%;
    left: -50%;
    margin-left: 25%;
    top: 0;
    background: {
      image: linear-gradient(to bottom, #1a2d78, #0c8faa); }
    border: {
      top-left-radius: 224% 280%;
      top-right-radius: 110% 90%; } }

  .wrap--day & {
    color: $sunColor;

    &::before {
      background-image: linear-gradient(to bottom, #d37e0a, #f0c41c 30%, #f3e733); }

    &::after {
      content: '';
      position: absolute;
      background: {
        image: url('../images/cal/daySun.png');
        repeat: no-repeat;
        size: cover; }
      width: 20vh;
      height: 20vh * (204 / 207);
      bottom: 10vh;
      right: -30vh;
      z-index: -1;
      transform: scale(0);
      will-change: transform, bottom, right; } }

  .day & {
    &::after {
      bottom: 65vh;
      right: 3.5vh;
      transform: scale(1);
      transition: bottom 1.2s, right 1.5s, transform 1.5s;
      transition-timing-function: ease-out; } } }

.page-footbox {
  position: relative;
  z-index: 1;
  height: $footArea;

  &::after {
    content: '';
    position: absolute;
    width: $towerWidth;
    height: $towerHeight;
    right: $towerMarginRight;
    bottom: 11.5vh;
    background: {
      size: cover; } }

  &::after {
    background: {
      image: url('../images/cal/tower-night.png'); }
    filter: drop-shadow(0 0 5px rgba($moonColor, .5)); }

  .wrap--day & {
    &::after {
      background-image: url('../images/cal/tower-day.png');
      filter: none; } } }

.buddha-info {
  display: flex;
  height: $footArea;
  align-items: center;
  padding-top: 3vh;

  .list {
    list-style: none;
    padding-left: 0;
    margin-bottom: 0;
    width: 100%;
    padding-left: 1.5em;
    font-size: 1.2em;

    li {
      width: 49%;
      display: inline-flex;
      align-items: center;
      padding-left: 0.5em; }

    .item {
      white-space: nowrap;
      text-overflow: ellipsis;
      overflow: hidden; } } }

// 白天埸景
.wrap--day {
  color: $dBaseColor; }

.bless__head,
.bless__main {
  position: relative;
  z-index: 1; }

.bless__head {
  padding-top: $margin; }

.bless__main {
  position: relative;
  z-index: 5;
  width: 55%;
  font-size: 1.1em;
  margin: {
    top: $margin;
    left: $margin; }

  p {
    position: relative;
    opacity: 0;
    top: -20%;
    text-align: justify;

    &::before {
      content: '';
      display: inline-block;
      width: .6em;
      height: .6em;
      border-radius: 50%;
      background-color: $sunColor;
      margin-left: 0.2em;
      margin-right: 0.2em; }

    @for $i from 1 through 5 {
      &:nth-child(#{$i}) {
        top: percentage(-($i) * 0.01);

        &::before {
          background-color: lighten($sunColor, (6 - $i) * 8); } } } }

  .fa-arrow-down {
    color: $sunColor; } }

.bless-title {
  color: $sunColor;
  margin-right: $margin;
  margin-left: $margin;
  display: flex;
  justify-content: center;
  align-items: center; }

.bless-moon {
  display: block;
  width: 40px;
  height: 40px;
  box-shadow: inset 10px -10px #eccc68;
  border-radius: 50%;
  filter: drop-shadow(0 0 3px $sunColor);
  margin-right: 10px;
  opacity: 0;
  transform: translateY(15px); }

.bless-numBox {
  width: 2em;
  display: block;
  position: relative; }

.bless-num {
  position: absolute;
  top: 50%;
  left: 50%;
  font-size: 1em;
  transform: translate(-50%, -50%); }

.wrap--day {
  .page-footbox {
    display: flex;

    &.active {
      &::after {
        bottom: 18.5vh; } } }

  .ripple {
    width: 20%;
    white-space: initial;
    padding-left: 0.5em;
    padding-right: 0.5em;
    font-size: 1em;
    opacity: 0; } }

.ytbox {
  position: relative;
  width: 80%;
  opacity: 0;

  iframe {
    position: absolute;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0; } }

@media(min-width: 500px) {
  body {
    font-size: 1.2em; } }
